*::selection{
    color: $white-color;
    background: $gold-color;
}
body{/*MIXIN PARA DEFINIR FONTS -> defineFont( ffamily, fweight, fsize)*/
    font-family: 'Akshar', sans-serif;
    font-weight: 300;
    font-size: 1.3rem;
}
.font-brand{/*MIXIN PARA DEFINIR FONTS -> defineFont( ffamily, fweight, fsize)*/ /*brand-text*/
    font-family: 'Courgette', cursive;
    /*color: white; -> ESTE HACERLO CON text-light -> clase de BOOTSTRAP*/
    font-size: 2rem;
}
.product-image{
    height: 180px;
	width: 180px;
	transition: 0.5s;
    border-radius: 4%;
    &:hover{
        transform: scale(1.3);
    }
}
.retroshadow { /*revisar si esto debe ir incluido debajo y que quede todo junto en esta class*/
	color: $white-color;
	letter-spacing: 0.7em;
    text-shadow: 2px 2px 0px #262525, 3px 3px 0px rgba(0, 0, 0, 0.2);
}
#banner span{
    /*MIXIN PARA DEFINIR FONTS -> defineFont( ffamily, fweight, fsize)*/
    font-family: "Avant Garde", Avantgarde, "Century Gothaic", CenturyGothic, "AppleGothic", sans-serif;
    font-size: 1.5rem;
    width: 80%;
    padding: 1.5rem 0.5rem;
    text-rendering: optimizeLegibility;
    border: .2rem solid $white-color;
    box-shadow: 0 0 .5rem 0.1rem black;
    color: #ffffff;/* -> ESTE HACERLO CON text-light -> clase de BOOTSTRAP*/
    @media all and ( min-width: 576px ){
        font-size: 2rem;
        text-shadow: 3px 3px 0px #262525, 5px 5px 0px rgba(0, 0, 0, 0.2); /*mixin -> defineTextShadow()*/
    }
    @media all and ( min-width: 768px ){
        padding: 1.5rem 1.7rem;
    }     
    @media all and ( min-width: 992px ){
        font-size: 2.5rem;
        text-shadow: 4px 4px 0px #262525, 7px 7px 0px rgba(0, 0, 0, 0.2); /*mixin -> defineTextShadow()*/
    }  
}
.card--mod{
    border: none;
    border-radius: 0;
}
.bg-gold-gradient{
    background: linear-gradient(to bottom, #ffffff, #cec3b1, #887145, #332202);
}
.btn-primary--mod{
    background: #8a857b;
    border-color: $gray-color;
    &:hover,&:focus{
        background-color: $gold-color;
        border-color: $light-yellow;        
    }
    &:focus{
        box-shadow: 0 0 0 .25rem rgba(77, 80, 83, 0.5);
    }
}
.bg-gray{
    background-color: #262626;
}
.bg-copyright{
    background-color: rgb(57, 57, 57);
}
.bg-light-gray{
    background-color: #ccc;
}
#follow-us{
    background: linear-gradient(90deg, rgb(131, 58, 180) 20%, rgb(253, 29, 29) 40%, rgb(252, 176, 69) 80%);
    animation: gradient 8s ease infinite;
	background-size: 400% 400%;
}
@keyframes gradient{
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}


