/*----------------------------*/
.navbar-toggler--mod{
    color: $gold-color !important;
    border-color: rgb(98, 78, 0) !important;
}
#menu > li:hover{
    background: $gold-color;
}
.carousel-control-prev-icon--mod{
    transform: rotate(270deg);
}
.carousel-control-next-icon--mod{
    transform: rotate(90deg);
}
/*MIXIN PARA DEFINIR FONTS -> defineFont( ffamily, fweight, fsize)*/ /*font-class*/
.font-section{
    font-family: 'Josefin Slab', serif;
    font-weight: 400;
    font-size: 1.5rem !important;
    @media all and ( min-width: 768px ){
        font-size: 2.5rem !important;
    }
}
.form-control--mod:focus{
    border-color: #887145 !important;
    box-shadow: none;
}
.font-color-highlight{ /*font-color-gold*/
    color: $gold-color;
}
@media all and (max-width: 390px){
    div.card {
        width: 100%;
    }
}
.form-check-input--mod:checked{
    background-color:#887145;
    border-color: $light-yellow;
}
.form-check-input--mod:focus{
    box-shadow: none;
    border-color: $gray-color;
}